.modal-overflow {
  position: fixed;
  display: table;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  .modal-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  .modal-content {
    position: relative;
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    height: 100%;
    opacity: 0;
    transform: scale(0.8);
    transition: opacity 0.2s ease, transform 0.3s ease;
    .modal-body {
      background: white;
      overflow: hidden;
      width: 270px;
      margin: 0 auto;
      padding: 20px;
    }
  }
  &.show {
    .modal-backdrop {
      opacity: 0.5;
      transition: opacity 0.5s ease;
    }
    .modal-content {
      opacity: 1;
      transform: scale(1);
      transition: opacity 0.43s ease, transform 0.4s ease;
    }
  }
}



