// buttons
.form-container {
  text-align: center;
  font-size: 1rem;
  .form-button {
  }
}
.form-input, .form-button {
  display: block;
  border: 1px solid lightblue;
  color: lightblue;
  margin: 10px auto;
  width: 280px;
  height: 40px;
  line-height: 40px;
  font-size: 0.8rem;
}
.form-input {
  background: white;
  text-indent: 10px;
}

.form-button {

  cursor: pointer;
  background: lightslategray;
  user-select: none;
  &.touchHover, &:hover {
    background: lightgoldenrodyellow;
  }
}



